'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const Link = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank">
            <i className={`icon ${this.props.icon}`}></i>
          </a>
        </div>
      </li>
    );
  }
});


export default React.createClass({
  getInitialState() {
    return {
      links: __links,
      status: 'loading',
      backdrop: React.PropTypes.string
    };
  },
  componentDidMount() {
    if (this.state.status === 'loading') {
      this.createLoader();
    }
  },
  createLoader() {
    this.img = new Image();
    this.img.addEventListener('load', () => {
      this.setState({ backdrop: this.img.src });
      this.refs.links.style.backgroundImage = this.state.backdrop;
    });
    this.img.src = require('images/site/banana-plants.png');
  },
  render() {
    return (
      <div ref="links" className="page-wrapper">
        <ul  className="link-items list-icons animated fadeIn">
          { this.state.links.map((result, i) => {
            return <Link key={ result.name } url={ result.url } icon={ result.icon } />;
          }) }
        </ul>
      </div>
    );
  }
});


